function resolveComWixCategoriesApiV1CategoriesServiceUrl(
  opts: Omit<ResolveUrlOpts, 'domainToMappings'>
) {
  const domainToMappings = {
    '*.dev.wix-code.com': [
      {
        srcPath: '/_api/categories',
        destPath: '',
      },
    ],
    'www.wixapis.com': [
      {
        srcPath: '/categories/v1/bulk/categories',
        destPath: '/v1/bulk/categories',
      },
      {
        srcPath: '/categories/v1/categories',
        destPath: '/v1/categories',
      },
    ],
    'manage._base_domain_': [
      {
        srcPath: '/_api/categories',
        destPath: '',
      },
    ],
    'editor._base_domain_': [
      {
        srcPath: '/_api/categories-service',
        destPath: '',
      },
    ],
    'blocks._base_domain_': [
      {
        srcPath: '/_api/categories-service',
        destPath: '',
      },
    ],
    'create.editorx': [
      {
        srcPath: '/_api/categories-service',
        destPath: '',
      },
    ],
    _: [
      {
        srcPath: '/categories/v1/bulk/categories',
        destPath: '/v1/bulk/categories',
      },
      {
        srcPath: '/categories/v1/categories',
        destPath: '/v1/categories',
      },
    ],
  };

  return resolveUrl(Object.assign(opts, { domainToMappings }));
}

